/* eslint-disable */

<template>
  <div class="hello">
    <h1>Register</h1>
    <form v-on:submit="saveUser">
      <input type="text" v-model="username" placeholder="Username..."/>
      <input type="password" v-model="password" placeholder="Password..."/>
      <select v-model="selectedTeam">
        <option disabled value="">Please select one</option>
        <option v-for="item in teams" v-bind:value="item">
          {{ item.teamName }}
        </option>
      </select>
      <span>Selected: {{ selectedTeam.teamName }}</span>
      <input type="submit" name="Submit" value="Register" />
    </form>
    <div>
      <router-link to="/">Go to log in</router-link>
    </div>
    <div>
      <router-link to="feelometer">Temporary link to feelometer</router-link>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    /* eslint-disable */
  name: 'RegisterUser',
  data() {
    return {
      username: '',
      password: '',
      teams: [],
      selectedTeam: {},
    }
  },
  methods: {
    getUsers: function () {
      axios.get('http://localhost:8080/user')
        .then(function(response) {
          console.log(response)
        })
    },
    saveUser: function (event) {
      event.preventDefault();
      axios.post('http://localhost:8080/user', {
        username: this.username, password: this.password, team: this.selectedTeam
      })
        .then(function(response) {
          console.log(response)
        })
    },
    getTeams: function() {
      axios.get('http://localhost:8080/team')
        .then((response) => {
          this.teams = response.data;
        });
    }
  },
    beforeMount () {
       this.getTeams()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



