/* eslint-disable */

<template xmlns:v-model="http://www.w3.org/1999/xhtml">
  <div class="feelometer">
    <h1>{{ message }}</h1>
    <div>
      <input
        type="radio"
        id="-2"
        value="-2"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="-2">-2</label>
      <input
        type="radio"
        id="-1"
        value="-1"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="-1">-1</label>
      <input
        type="radio"
        id="0"
        value="0"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="0">0</label>
      <input
        type="radio"
        id="1"
        value="1"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="1">1</label>
      <input
        type="radio"
        id="2"
        value="2"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="2">2</label>
      <input
        type="radio"
        id="3"
        value="3"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="3">3</label>
      <input
        type="radio"
        id="4"
        value="4"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="4">4</label>
      <input
        type="radio"
        id="5"
        value="5"
        v-model= "chosenScore"
        class="scoreRadioButton"
        v-on:change="saveScore"
      />
      <label for="5">5</label>
    </div>
    <div><h2>Du har valt: {{chosenScore}}</h2></div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    /* eslint-disable */
    name: 'Feelometer',
    data() {
      return {
        message: 'Hur k√§nner du dig idag?',
        scores : [-2,1,0, 1, 2, 3, 4, 5],
        chosenScore: ""
      }
    },
    methods: {
      saveScore: function() {
        console.log(this.chosenScore);
        axios.get('http://localhost:8080/user/'+ this.getCookie('userId') +'/score?score=' + this.chosenScore)
          .then(function(response) {
            console.log(response)
          })
      },
      getCookie: function(name) {
        let regexp = new RegExp("(?:^" + name + "|;\s"+ name + ")=(.?)(?:;|$)", "g");
        let result = regexp.exec(document.cookie);
        return (result === null) ? null : result[1];
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }

  label {
    border: 2px solid grey;
    font-size: 80px;
    background-color: lavender;
  }
  .scoreRadioButton{
    visibility: hidden;
  }
</style>



